body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App-logo {
  height: 40vmin;
}

.App-header p {
  max-width: 650px;
  padding: 0 20px;
}
.App-header {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.App-arrow {
  display: block;
  position: absolute;
  height: 40px;
  width: 40px;
  z-index: 100;
  background-size: 100% auto;
  bottom: -18px;
  left: 50%;
  margin-left: -20px;
  animation: animate 2s infinite;
}
.App-arrow::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  margin-left: -10px;
  border-top: 3px solid #ccc;
  border-left: 3px solid #ccc;
  transform: rotate(45deg)
}
@keyframes animate {
  0% {
    opacity:0;-webkit-transform:translateY(0);transform:translateY(0)
  }
  33%,66% {
    opacity:.8
  }
  50% {
    opacity:.8;-webkit-transform:translateY(-8px);transform:translateY(-8px)
  }
  100% {
    opacity:0;-webkit-transform:translateY(-16px);transform:translateY(-16px)
  }
}
@-webkit-keyframes animate {
  0% {
    opacity:0;-webkit-transform:translateY(0)
  }
  33%,66% {
    opacity:.8
  }
  50% {
    opacity:.8;-webkit-transform:translateY(-8px)
  }
  100% {
    opacity:0;-webkit-transform:translateY(-16px)
  }
}